// Функция для отображения ошибки
const showError = (formElement, inputElement, errorMessage, validationConfig) => {
  const errorElement = formElement.querySelector(`.${inputElements.id}-error`);
  inputElement.classList.add(validationConfig.inputElement);
  errorElement.textContent = errorMessage;
  errorElement.classList.add(validationConfig.errorClass);
}

// Функция для очистки ошибок
const hideError = (formElement, inputElement, validationConfig) => {
  const errorElement = formElement.querySelector(`.${inputElement.id}-error`);
  inputElement.classList.remove(validationConfig.inputErrorClass);
  errorElement.classList.remove(validationConfig.errorClass);
  errorElement.textContent = "";
}

const hasInvalidInput = (inputList) => {
    return inputList.some(inputElement => !inputElement.validity.valid);
};

const isValid = (formElement, inputElement, validationConfig) => {
  const regex = /^[a-zA-Zа-яА-ЯёЁ0-9\- ]+$/;
  const errorMessage = "Разрешены только латинские, кириллические буквы, знаки дефиса и пробелы";
    if (!regex.test(inputElement.value)) {
      inputElement.setCustomValidity(errorMessage);
    } else {
      inputElement.setCustomValidity("");
    }
    if (!inputElement.validity.valid) {
      showError(formElement, inputElement, inputElement.validationMessage || errorMessage, validationConfig);
    } else {
      hideError(formElement, inputElement, validationConfig);
    }
};

const toggleButtonState = (inputList, buttonElement, validationConfig) => {
  if (hasInvalidInput(inputList)) {
    buttonElement.disabled = true;
    buttonElement.classList.add(validationConfig.inactiveButtonClass);
  } else {
    buttonElement.disabled = false;
    buttonElement.classList.remove(validationConfig.inactiveButtonClass);
  }
};

const setEventListeners = (formElement, validationConfig) => {
  const inputList = Array.from(formElement.querySelectorAll(validationConfig.inputSelector));
  const buttonElement = formElement.querySelector(validationConfig.submitButtonSelector);

  toggleButtonState(inputList, buttonElement, validationConfig);

  inputList.forEach((inputElement) => {
    inputElement.addEventListener("input", () => {
      isValid(formElement, inputElement, validationConfig);
      toggleButtonState(inputList, buttonElement, validationConfig);
    });
  });
};

export const enableValidation = (validationConfig) => {
  const formList = Array.from(formElement.querySelectorAll(validationConfig.formSelector));

    formList.forEach((currentFormElement) => {
      setEventListeners(currentFormElement, validationConfig);
  });
};

export const clearValidation = (formElement, validationConfig) => {
  const inputList = Array.from(formElement.querySelectorAll(validationConfig.inputSelector));
  
    inputList.forEach((inputElement) => {
      hideError(formElement, inputElement, validationConfig);
        inputElement.setCustomValidity(""); 
  });

    const buttonElement = formElement.querySelector(validationConfig.submitButtonSelector);
    buttonElement.disabled = true;
    buttonElement.classList.add(validationConfig.inactiveButtonClass);
};